      <div className="flex gap-4">
        <Button className="bg-orange-500 hover:bg-orange-600">
          Nova Transferência
        </Button>
        <Button className="bg-orange-500 hover:bg-orange-600">
          Pagar Conta
        </Button>
        <Button className="bg-orange-500 hover:bg-orange-600">
          Ver Extrato
        </Button>
      </div>

